@font-face {
    font-family: 'Courier New';
    src: url('/src/fonts/courierNew/CourierNewPSMT.eot');
    src: local('Courier New'), local('CourierNewPSMT'),
        url('/src/fonts/courierNew/CourierNewPSMT.eot?#iefix') format('embedded-opentype'),
        url('/src/fonts/courierNew/CourierNewPSMT.woff2') format('woff2'),
        url('/src/fonts/courierNew/CourierNewPSMT.woff') format('woff'),
        url('/src/fonts/courierNew/CourierNewPSMT.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }